@import url(https://fonts.googleapis.com/css2?family=PT+Sans&family=Poppins:wght@200;300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.about-container {
  height: calc(100vh - 169px);
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #aaa;
}

.about-card {
  width: 55em;
  height: 25em;
  background-color: rgb(83, 83, 83);
  color: white;
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
  border-radius: 4px;
}

.about-text {
  padding: 4em;
}

.tech-list {
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 1em;
}

.tech-list li {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.2em;
}

.react-icon {
  width: 3em;
  height: 2em;
  color: #ffcc54;
}

.about-pages {
  display: flex;
  justify-content: space-evenly;
  margin-top: 1em;
  list-style: none;
}

.about-pages li a {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.2em;
  color: white;
  text-decoration: none;
}

.about-pages li a:hover {
  color: black;
}

.social-links-icons {
  width: 30px;
  height: 30px;
  background-color: white;
  border-radius: 3px;
  margin-right: 8px;
}

@media (max-width: 599px) {
  .about-container {
    height: 100vh;
  }
  /*  .about-text {
    width: 100vw;
  } */
  .about-card {
    height: auto;
    width: 100%;
    padding: 1em;
  }
  .about-text {
    padding: 0;
  }
  .tech-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .tech-list li a {
    justify-content: initial;
  }
  .about-pages {
    /*  display: grid;
    grid-template-columns: 1fr; */
  }
}

.no-recipes-container {
  height: 20em;
  width: 40em;
  margin: 1em;
  padding: 1em;
  background-color: rgba(245, 245, 245, 0.603);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  font-weight: 600;
  flex-direction: column;
  text-align: center;
  box-shadow: 3px 3px 5px 1px #1c1a1a;
}
.card-conteiner {
  background-color: #fff;
  font-family: "Poppins", sans-serif;
  margin: 30px;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 3px 3px 10px 3px rgb(0 0 0);
  overflow: hidden;
  cursor: default;
  transition: all 300ms ease;
  text-align: center;
  min-height: 330px;
  width: 250px;
  max-height: 390px;
}

.card-conteiner:hover {
  box-shadow: 6px 6px 20px 5px rgb(0, 0, 0, 1);
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

.card-image {
  width: 270px;
  min-height: 231px;
  margin: -10px 0px 0px -10px;
}

.card-title {
  font-size: 15px;
  font-weight: 600;
  height: 67px;
  display: grid;
  justify-content: center;
  align-items: center;
}

.card-diets {
  min-height: 38px;
  display: grid;
  align-content: center;
  justify-content: center;
}

.navlink-card {
  text-decoration: none;
  color: rgb(0, 0, 0);
}

.hr-card {
  border: 1px solid #a84242d9;
  width: 85%;
  margin-top: 0;
}

.cards-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  background-color: rgb(187 187 187 / 60%);
  min-height: calc(100vh + 122px);
  background-image: url(/static/media/wood-table-home.30b17268.png);
}

.paginationBttns {
  height: 45px;
  list-style: none;
  display: flex;
  justify-content: center;
  background: #853535;
  align-items: center;
  padding: 5px 0px 5px 0px;
  margin: 0;
}

.paginationBttns .paginationActive li {
  display: none;
}

.paginationBttns a {
  padding: 10px;
  margin: 8px;
  border-radius: 3px;
  background-color: #fff;
  color: black;
  cursor: pointer;
  font-weight: 600;
}

.paginationBttns a:hover {
  color: white;
  background-color: rgba(3, 3, 3, 0.815);
}

.paginationActive a {
  color: white;
  background-color: rgba(3, 3, 3, 0.815);
}

.previousBttn a {
  width: 1px;
}

.pageLinkClassName {
  color: blue;
}

@media (max-width: 600px) {
  .pageLinkClassName li {
    display: none;
  }
}

.body-orders {
  display: flex;
  justify-content: center;
}

@media (max-width: 917px) {
  .body-orders {
    display: grid;
  }
  .body-orders .filter-button {
    margin: 4px;
    width: 80vw;  
  }
}

.searchbar-body input {
  border-radius: 2px;
  width: 250px;
  height: 30px;
  padding: 0px 0px 0px 10px;
  font-size: 16px;
  font-family: Arial, Helvetica, sans-serif;
  border: none;
  box-shadow: 1px 2px 3px 0px #0000008a;
  outline: 0;
}

.search-button {
  background: none;
  color: #853535;
  top: -25px;
  position: relative;
  left: 232px;
  cursor: pointer;
  font-size: 15px;
  border: none;
  top: 2px;
  position: relative;
  left: -30px;
}

@media (max-width: 917px) {
  .search-button {
    display: none;
  }
  .searchbar-body input{
    margin: 10px;
    justify-content: center;
    display: flex;
    width: auto;
  }
}

.general-filter {
  display: flex;
  align-items: center;
  background-color: rgba(40, 40, 40, 0.87);
  justify-content: space-evenly;
  border-bottom: 0;
  margin-top: 4px;
}

.body-filterbar {
  display: flex;
  margin: 10px;
  
}

.filter-button {
  display: flex;
  border: none;
  margin: 0px 5px 0px 5px;
  border-radius: 3px;
  align-items: center;
  height: 3em;
  cursor: pointer;
  background-color: #222222a3;
  box-shadow: 1px 2px 3px 0px #000000c7;
  justify-content: center;
  width: 65px;
  color: white;
}
.filter-button:hover {
  background-color: #a8a8a891;
  color: black;
  transition: 0.2s;
}

.filter-button-reset {
  display: flex;
  border: none;
  margin: 0px 5px 0px 5px;
  border-radius: 3px;
  align-items: center;
  height: 3em;
  cursor: pointer;
  box-shadow: 1px 2px 3px 0px #0000008a;
  justify-content: center;
  width: 65px;
  background-color: #ba4343;
  color: white;
}

.filter-button-reset:hover {
  color: black;
  background-color: #e86969;
}

.form-filter {
  display: flex;
  
}

.select-filter {
  border: none;
  background-color: white;
  cursor: pointer;
  box-shadow: 1px 2px 3px 0px #0000008a;
  border-radius: 3px;
  
}

.order-filter-body {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  
}

.mobile-button {
  display: none;
}

.cerrado  {
  display: flex;
  align-items: center;
  
}

@media (max-width: 917px) {
  .order-filter-body {
    display: grid;
    justify-items: center;

  }

  .mobile-button {
    width: 48px;
    height: 42px;
    background: none;
    border: none;
    cursor: pointer;
    color: #ffffffbf;
    margin: 5px;
    padding-top: 5px;
    display: inline;
    
  }

  .general-filter {
    display: grid;
    justify-content: center;
    align-items: center;
    
  }

  .searchbar-filter {
    align-items: center;
    display: flex;
    justify-content: center;
  }

  .cerrado  {
    top: -100%;
    transition: all .5s;
    position: fixed;
  
  }
}

.back-button {
  width: 8em;
  height: 2.5em;
  border: none;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: #242424;
  cursor: pointer;
  margin: 0 1em 1em 0;
  font-size: 1.2em;
  box-shadow: 2px 2px 3px 2px black;
}

.back-button-container {
  display: flex;
  justify-content: flex-end; 
  margin-right: 4em;
}

@media (max-width: 899px){
  .back-button-container {
    justify-content: center;
    margin: 0;
  }
  .back-button {
    margin: 0;
  }
}

.errors-message {
    color: red;
    font-style: italic;
  }
  
.form-container {
  margin-right: auto;
  margin-left: auto;
  padding: 1.5em;
  max-width: 1170px;
  box-shadow: 0 0 25px 0 rgb(0, 0, 0);
  border-radius: 0 18px 18px 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  min-height: 879px;
  background-image: url(/static/media/paper-background.a9387438.jpg);
}

.form-container > * {
  padding: 1em;
}

.form-container input,
textarea,
select {
  width: 100%;
  border: none;
  background: none;
  border-bottom: 1px solid black;
  outline: 0;
}

.form-container label {
  font-weight: 600;
}

.select-field {
  border: 1px solid black;
  width: -webkit-max-content;
  width: max-content;
}

.form-container .button-form:hover,
.form-container .button-form:focus {
  color: white;
  background-color: #8f4444;
  transition: background-color 0.5s ease-in-out;
  outline: none;
}

.form-container .button-form {
  background: #853535;
  border: 0;
  text-transform: uppercase;
  cursor: pointer;
  color: #000;
  border-radius: 4px;
  height: 40px;
  width: 200px;
}

.block {
  grid-column: 1/3;
}

.block-button {
  margin: 0;
  grid-column: 1/3;
  text-align: center;
}

.form-container .disabled {
  background-color: grey;
  border: 0;
  text-transform: uppercase;
  cursor: default;
  color: #000;
  border-radius: 4px;
  height: 40px;
  width: 200px;
}

.form-container .disabled:hover {
  background-color: grey;
  color: black;
}

.diets-buttons-container {
  display: grid;
  grid-column-end: 3;
  padding: 0;
}

.diets-buttons {
  padding: 0;
  background: #393939;
  color: white;
  width: 50%;
  text-align: center;
}

.diets-button-a {
  display: flex;
  margin: 4px;
  justify-content: center;
}

.close-diets-button {
  background-color: #993333;
  color: white;
  border: none;
}

@media (min-width: 801px) {
  .form-container {
    width: 700px;
  }
}

@media (max-height: 799px) {
  .form-container {
    min-height: 904px;
   
  }
}

@media (max-width: 899px) {
  .form-container {
    display: block;
    margin-top: 1em;
  }
  .form-container .button-form {
    width: 100%;
  }
}

