.general-filter {
  display: flex;
  align-items: center;
  background-color: rgba(40, 40, 40, 0.87);
  justify-content: space-evenly;
  border-bottom: 0;
  margin-top: 4px;
}

.body-filterbar {
  display: flex;
  margin: 10px;
  
}

.filter-button {
  display: flex;
  border: none;
  margin: 0px 5px 0px 5px;
  border-radius: 3px;
  align-items: center;
  height: 3em;
  cursor: pointer;
  background-color: #222222a3;
  box-shadow: 1px 2px 3px 0px #000000c7;
  justify-content: center;
  width: 65px;
  color: white;
}
.filter-button:hover {
  background-color: #a8a8a891;
  color: black;
  transition: 0.2s;
}

.filter-button-reset {
  display: flex;
  border: none;
  margin: 0px 5px 0px 5px;
  border-radius: 3px;
  align-items: center;
  height: 3em;
  cursor: pointer;
  box-shadow: 1px 2px 3px 0px #0000008a;
  justify-content: center;
  width: 65px;
  background-color: #ba4343;
  color: white;
}

.filter-button-reset:hover {
  color: black;
  background-color: #e86969;
}

.form-filter {
  display: flex;
  
}

.select-filter {
  border: none;
  background-color: white;
  cursor: pointer;
  box-shadow: 1px 2px 3px 0px #0000008a;
  border-radius: 3px;
  
}

.order-filter-body {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  
}

.mobile-button {
  display: none;
}

.cerrado  {
  display: flex;
  align-items: center;
  
}

@media (width < 918px) {
  .order-filter-body {
    display: grid;
    justify-items: center;

  }

  .mobile-button {
    width: 48px;
    height: 42px;
    background: none;
    border: none;
    cursor: pointer;
    color: #ffffffbf;
    margin: 5px;
    padding-top: 5px;
    display: inline;
    
  }

  .general-filter {
    display: grid;
    justify-content: center;
    align-items: center;
    
  }

  .searchbar-filter {
    align-items: center;
    display: flex;
    justify-content: center;
  }

  .cerrado  {
    top: -100%;
    transition: all .5s;
    position: fixed;
  
  }
}
